<template>
  <div class="hot_header_module">
    <div class="hot_header">
      <div class="hot_header_left logo" @click="toPage('homepage')"></div>
      <div class="hot_header_right hot_header_row">
        <div
          :class="['hot_header_col', currRouteName === 'lp' ? 'is_active' : '']"
          @click="toPage('lp')"
        >
          LP区
        </div>
        <div
          :class="[
            'hot_header_col',
            currRouteName === 'pledge' ? 'is_active' : '',
          ]"
          @click="toPage('pledge')"
        >
          质押池
        </div>
        <!-- <div :class="['hot_header_col', currRouteName === 'dao' ? 'is_active' : '']" @click="toPage('dao')">DAO</div> -->
        <div
          :class="[
            'hot_header_col',
            currRouteName === 'mine' ? 'is_active' : '',
          ]"
          @click="toPage('mine')"
        >
          我的
        </div>
      </div>
    </div>
    <div class="hot_header">
      <div class="hot_header_left"></div>
      <div class="hot_header_right hot_header_row">
        <div class="hot_header_col address" @click="toPage('mine')">
          <div v-if="showEnabled">
            <span class="is_enabled" v-if="hotBalance >= 500">已激活</span>
            <span class="not_enabled" v-else>未激活</span>
          </div>

          {{ address | addressFormat }}
        </div>
        <div class="hot_header_col tag">EN</div>
      </div>
    </div>
  </div>
</template>

<script>
import Net from "@/assets/net.js";
export default {
  data() {
    return this.$Config;
  },

  mounted() {
    var _this = this;
    
   
    if (typeof window.web3 != "undefined") {
      console.log(JSON.stringify(web3.currentProvider) +"ddddddd")
      console.log("window.ethereum"+JSON.stringify(window.ethereum))
      this.$getAddress();
      setTimeout(function () {
        _this.getHotBalance();
      }, 1000);
    }
  },

  computed: {
    currRouteName() {
      return this.$route.name;
    },
  },

  filters: {
    addressFormat(addr) {
      if (!addr) return "";
      const newAddr = addr;
      if (newAddr.length > 8) {
        return `${newAddr.substr(0, 4)}...${newAddr.substr(
          newAddr.length - 4,
          4
        )}`;
      }
      return addr;
    },
  },
  methods: {
    toPage(name) {
      // if (name === 'dao') return Message.warning({
      //   type: 'warning',
      //   message: '暂未开放',
      //   customClass: 'notity_message',
      //   showClose: true,
      //   // duration: 0
      // })
      this.$router.push({ name });
    },
    getHotBalance() {
      //hot合约余额
      var _this = this;

     
      var hotContract = new web3.eth.Contract(Net.ERC20abi, Net.HOG);
      hotContract.methods
        .balanceOf(_this.address)
        .call({ from: _this.address })
        .then(function (res) {
          _this.showEnabled = true;
          _this.hotBalance = res / 1e18;
          console.log("顶部HOT地址余额：" + _this.hotBalance);
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.hot_header_module {
  padding: 30px 23px 0;
}
.hot_header {
  display: flex;
  .hot_header_left {
  }
  .logo {
    width: 78px;
    height: 35px;
    background: url("../assets/image/logo.png") no-repeat;
    background-size: 100% 100%;
  }
  .hot_header_right {
    flex: 1;
    text-align: center;
  }
}
.hot_header_row {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  .hot_header_col {
    cursor: pointer;
    margin: 0 12px;
    display: inline-block;
    min-width: 30px;
    font-size: 14px;
    font-family: PingFangSC-Semibold, PingFang SC;
    font-weight: 600;
    color: #70888f;
    border-bottom: 4px solid transparent;
    padding: 0 3px;
  }
  .is_active {
    color: #41c6a6;
    border-bottom: 3px solid #41c6a6;
  }
  .is_enabled {
    font-size: 11px;
    font-weight: 400;
    color: #41c6a6;
    line-height: 15px;
    font-family: PingFangSC-Regular, PingFang SC;
    border: 1px solid #41c6a6;
    border-radius: 3px;
    margin-right: 5px;
  }
  .not_enabled {
    font-size: 11px;
    font-weight: 400;
    color: #aeaeae;
    line-height: 15px;
    font-family: PingFangSC-Regular, PingFang SC;
    border: 1px solid #aeaeae;
    border-radius: 3px;
    margin-right: 5px;
  }
  .address {
    color: #666;
    font-size: 12px;
    font-weight: 500;
    border: none;
    margin-top: 10px;
    display: flex;
  }
  .tag {
    border: none;
    padding: 2px;
    margin-top: 10px;
    background: rgba(#fff, 0.58);
    border-radius: 8px;
    color: #333;
    line-height: 20px;
    font-size: 14px;
    font-weight: 500;
  }
}
</style>


